.single {
    display: flex;
    width: 100%;
  
    .singleContainer {
      flex: 6;
  
      .top {
        padding: 20px;
        display: flex;
        gap: 20px;
  
        .left {
          flex: 1;
          -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
          box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
          padding: 20px;
          position: relative;
          align-items: flex-start;
          .editButton {
            position: absolute;
            top: 0;
            right: 0;
            padding: 5px;
            font-size: 12px;
            color: #7451f8;
            background-color: #7551f818;
            cursor: pointer;
            border-radius: 0px 0px 0px 5px;
          }
  
          .item {
            display: flex;
            gap: 20px;
  
            .itemImg {
              width: 100px;
              height: 100px;
              border-radius: 50%;
              object-fit: cover;
            }
  
            .details {
              .itemTitle {
                margin-bottom: 10px;
                color: #555;
              }
  
              .detailItem {
                margin-bottom: 10px;
                font-size: 14px;
  
                .itemKey {
                  font-weight: bold;
                  color: gray;
                  margin-right: 5px;
                }
  
                .itemValue {
                  font-weight: 300;
                }
              }
            }
          }
        }
  
        .right {
          flex: 2;
        }
      }
  
      .bottom {
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        padding: 20px;
        margin: 10px 20px;
  
        .table {
          .cellWrapper {
            display: flex;
            align-items: center;
  
            .image {
              width: 32px;
              height: 32px;
              border-radius: 50%;
              margin-right: 10px;
              object-fit: cover;
            }
          }
  
          .status {
            padding: 5px;
            border-radius: 5px;
  
            &.Approved {
              color: green;
              background-color: rgba(0, 128, 0, 0.151);
            }
            &.Pending {
              color: goldenrod;
              background-color: rgba(189, 189, 3, 0.103);
            }
          }
        }
        .search-div {
          display: flex;
          align-items: center;
          border: 1px solid #ccc;
          padding: 5px;
          border-radius: 5px;
          margin-bottom: 10px;
          input {
            flex-grow: 1;
            border: none;
            outline: none;
            margin-right: 5px;
          }
          .search-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #eee;
            border-radius: 50%;
            height: 30px;
            width: 30px;
          }
        }
      }
  
      .title {
        font-size: 16px;
        color: lightgray;
        margin-bottom: 20px;
      }
    }
  }
  